@import url('https://fonts.googleapis.com/css2?family=Alice&family=Marck+Script&family=Noto+Sans&family=Noto+Sans+Mono&display=swap');

@font-face {
    font-family: 'aliceregular';
    src: url('../fonts/alice-regular-webfont.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'marck_scriptregular';
    src: url('../fonts/marckscript-regular-webfont.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'noto_sansregular';
    src: url('../fonts/notosans-regular-webfont.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'noto_monoregular';
    src: url('../fonts/notomono-regular-webfont.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

$bodybg: url("../images/redline.svg") 86.5% top / 2px repeat-y, url("../images/rightboder.svg") 100% top / 1px repeat-y, linear-gradient(to left, #f5f5f599, #ffffff00 4%, #ffffff00 96%, #f5f5f5), url("../images/kletka.svg") left top / auto 20rlh repeat,  #fefefe;

$bodybgsmall: url("../images/rightboder.svg") 100% top / 1px repeat-y, linear-gradient(to left, #f5f5f599, #ffffff00 4%, #ffffff00 96%, #f5f5f5), url("../images/kletka.svg") left top / auto 20rlh repeat,  #fefefe;

$headerbg: url("../images/topchik.svg") center top / 100% 100% no-repeat, url("../images/012345.png") center top / 100% 100% no-repeat, #fefefe;

$footerbg: url("../images/dungeon.svg") center bottom / 100% 100% no-repeat, #fefefe;

@import "main";
