
.mobile-menu
	display: none
	align-self: flex-end
	cursor: pointer
	inline-size: 2.75rem
	block-size: 2.75rem
	margin-bottom: 0.25rem
	border: 3px solid rgba($mainColor, 0.25)
	background: rgba(0, 0, 255, 0.1)
	border-radius: 0.5rem
	font-size: 2.25rem
	line-height: 1
	color: $mainColor
	caret-color: transparent
	outline: none
	
	&:focus
		color: #800000
		border-color: #80000075

#main-nav
	align-self: flex-end
	margin-bottom: 0.25rem
	
	.allposts
		display: none

	.main-cat
		display: flex
		column-gap: 0.25rem
		list-style-type: none

	a
		display: block
		font-family: $fontAlice
		font-size: 1.375rem
		text-align: center
		line-height: 1.1818
		padding: 0.25rem 0.75rem
		border: 3px solid rgba($mainColor, 0.25)
		border-radius: 1.25rem
		background-color: #ffffff55
		color: $mainColor
		text-decoration: none
		caret-color: transparent

		&:visited
			color: $mainColor

		&:focus
			outline: 0
			border-color: #80000075

		&.activePage:visited
			color: #800000

		&.activePage::before
			display: inline-block
			position: relative
			top: 0.0625rem
			content: "\1F31F"
			font-size: 1.15rem
			line-height: 1
			margin-right: 0.25rem

	.main-cat .postscat
		color: darkred
				
		&::after
			content: " \2026"
			color: darkred
			margin-left: 0

@media screen and (max-width: 800px)
	
	.mybackground
		background: $bodybgsmall
		overflow-x: hidden
	
	#myhello
		.scarec
			block-size: 4.5rem
			margin-right: 0.5rem

	main
		flex: 1
		margin-right: 1.5%
	
	aside
		display: none

	.mobile-menu
		display: flex
		justify-content: center
		align-items: center

	#main-nav
		visibility: hidden
		position: absolute
		top: 4.25rem
		right: 0
		max-inline-size: 75vw
		padding-top: 0.25rem
		z-index: 1
		opacity: 0
		transform: translateX(100%)
		transform-origin: right top
		transition-property: transform, visibility, opacity
		transition-duration: 500ms

		a
			max-inline-size: 72vw
			min-inline-size: 11ch

		.main-cat
			flex-flow: column nowrap
			column-gap: unset
			padding: 0.35rem 1.5rem 0.25rem 0.25rem
			background-color: #fff
		
			.menu-item ~ .menu-item
				padding-top: 0.5rem
					
		.main-cat .menu-item a
			padding: 0.5rem 0.25rem
			font-size: 1.5rem
			line-height: 1
			border:  unset
			box-shadow: inset 0 0 0.65rem rgba(0, 0, 205, 0.75)
			overflow-x: hidden
			text-overflow: ellipsis

			&:focus
				outline: solid #80000075

			&.postscat
				background-color: #ffff0005
				box-shadow: inset 0 0 0.75rem 0.1rem #80000075

		.main-cat .allposts
			display: flex
			flex-flow: column nowrap
			margin-left: 1rem
			padding: 0 0.175rem 0.25rem 0
			border-left: 4px solid #80000075
			max-block-size: 20rem
			overflow-y: auto

			.menu-item .activePost
				background: url("../images/asign.svg") left center / 0.75rem 0.75rem no-repeat, #ffff0008
				color: darkred

			.menu-item
				padding-left: 0
				padding-top: 0.25rem
			
				~ .menu-item
					border-top: thin solid #800000

				a
					display: block
					color: rgb(0,0,205)
					padding: 0.5rem	1rem
					font-size: 1.3125rem
					border-radius: unset
					box-shadow: none
					background-color: #ffff0008
				
	.mobile-menu:is(:hover, :focus) + #main-nav, #main-nav:is(:hover, :focus-within)
		transform: translateX(0)
		visibility: visible
		opacity: 1

#category-nav
	display: flex
	flex-flow: column nowrap
	padding: 0.1rem 0
	border: none
	margin-top: 1.105rem

	.allposts
		list-style-type: none
		padding-left: 0
		margin-left: 0

		.menu-item
			text-align: center
			margin-bottom: 0.65rem

			a
				display: block
				text-decoration: none
				overflow-x: hidden
				text-overflow: ellipsis
				font-family: $fontAlice
				font-size: 1.15rem
				padding: 0.275rem 0.25rem 0.575rem 1rem
				background-color: #ffff0025
				color: rgb(0,0,205)

			a:focus
				outline: medium solid #80000075

			a:last-child
				margin-bottom: 0

	.allposts .menu-item .activePost
		border: 4px solid #80000075
		border-right: none
		border-top-left-radius: 1.5rem
		border-bottom-left-radius: 1.5rem
		background: url("../images/asign.svg") left center / 0.75rem 0.75rem no-repeat
		padding: calc(0.275rem - 4px) 0.25rem calc(0.575rem - 4px) calc(1rem - 4px)
		background-color: #ffff0025
		color: #800000


