#showDialog
	display: inline
	cursor: pointer
	line-height: 1.175rem
	padding: 0rem 0.15rem
	background-color: #ffffff00
	border: 1px dotted #4b0082
	font-size: 1rem
	color: $mainColor

#greeting
	margin: auto auto
	border: 0.35rem double #4b0082
	border-radius: 1rem
	background-color: #ffffff
	
	&::backdrop
		background-color: rgba(0,0,0,0.85)

	#inputName
		padding: 2.5rem 0.5rem 1rem 0.5rem

	label
		display: block
		padding-bottom: 0.1rem
		font-family: $fontAlice
		font-size: 1.5rem
		color: $mainColor

	#insertName
		background-color: #dcdcdc
		font-size: 1.15rem
		font-family: $fontAlice
		padding: 0.05rem .35rem
		border-color: #191970
		border-style: dotted
		border-radius: .65rem
		caret-color: #4b0082
		margin: 0.5rem 0 1rem
		box-sizing: border-box

		&:focus
			background-color: #f5f5f5
			outline: none

	.errStyle
		border-color: crimson
		border-style: solid
		box-shadow: 0 0 5px 3px crimson
		
	#confirmBtn
		display: inline-block
		outline: none
		cursor: pointer
		font-size: 1.2rem
		font-weight: bolder
		padding: .1rem .4rem
		margin-left: 0.5rem
		border: 1px solid #000000
		border-radius: .75rem
		background-color: #ffffff
		box-shadow: inset 0 0 3px 1px #000000
		
		&:hover, &:focus
			color: rgb(0,100,0)
			box-shadow: inset 0 0 3px 1px rgb(0,100,0)
			border: 1px solid rgb(0,100,0)

	.cancel
		display: block
		cursor: pointer
		position: absolute
		inline-size: 1.75rem
		top: 0.125rem
		right: 0.125rem
		background: none
		border: 3px solid rgba($mainColor, 0.25)
		border-radius: 0.5rem
		text-align: center
		color: $mainColor
		
		&:hover, &:focus
				border: 3px solid red
				color: red
		
		span
			font-size: 1.5rem
			line-height: 1

	.error
		display: block
		position: absolute
		bottom: 3.75rem
		left: 1px
		inline-size: 13.3rem
		padding: 0
		font-family: $fonthtml
		font-size: 95%
		line-height: 1.25
		color: #ffffff
		background-color: rgba(0,0,0,0.75)
		border-radius: 0.3rem
		box-sizing: border-box
		
	.active
		padding: 0.5rem

	.color
		background-color: rgba(128,0,0,0.75)
	

