
$fonthtml: "noto_sansregular", "Noto Sans", sans-serif
$fontAlice: "aliceregular", "Alice", serif
$fontContent: "marck_scriptregular", "Marck Script", "Brush Script MT", cursive
$fontMono: "noto_monoregular", "Noto Sans Mono", "Courier New", "Lucida Console", monospace
$fontEmoji: emoji
$htmlbgc: rgb(254, 254, 254)
$mainColor: rgb(25, 25, 112)

*
	padding: 0
	margin: 0
	box-sizing: border-box

html
	background-color: $htmlbgc
	font-family: $fonthtml
	font-size: 1rem
	line-height: 1.5rem
	color: $mainColor

h1, h2, h3, h4, h5, h6
	font-family: $fontAlice

h1, h2, h3, h4
	line-height: 2rlh

h5, h6
	line-height: 1rlh

h1
	font-size: 2.5rem
	padding: 1.8rem 0 1.2rem

h2
	font-size: 2rem
	padding: 0.4rem 0 1.1rem

h3
	font-size: 1.75rem
	padding: 0.55rem 0 0.95rem

h4
	font-size: 1.5rem
	padding: 0.6rem 0 0.9rem

h5
	font-size: 1.25rem
	padding: 1.4rem 0 0.1rem

h6
	font-size: 1.15rem
	padding: 1.45rem 0 0.05rem

img
	max-inline-size: 100%

p
	hyphens: auto

a, a:visited
	color: mediumblue
	caret-color: transparent

a:focus
	outline: 2px solid #80000075

li::marker
	line-height: 1.25rem

.itemList
	ul, ol
		list-style-type: none
		margin: 0
		padding: 0
		
		li h2
			margin-left: 2rem

.symbol
	font-family: $fontEmoji

.emoji
	font-family: $fontEmoji
	font-size: 1.25rem
	line-height: 0.5

q
	font-style: italic

.fontcode
	font-family: $fontMono
	line-height: 1.25rem
	color: #000000

code
	font-family: $fontMono
	font-size: 0.95rem
	line-height: 1.25rem
	padding: 0 0.25rem
	border-radius: 0.25rem
	border: 2px solid #00000015
	background-color: #00000010
	color: #000000

.highlighter-rouge
	margin: 0.375rem 0

pre
	padding: calc(0.375rem - 2px) 0.75rem

pre > code
	padding: unset
	background-color: unset
	border: unset

.rightside
	padding: 1.5rem 10% 0 38%

table
	border-collapse: collapse
	margin: 0.75rem 0 calc(0.75rem - 1px) 0

	td, th
		border: 1px solid black

	td, th
		block-size: calc(3rem - 1px)
		padding: 0 1rem

th
	font-size: large

.mybackground
	display: flex
	flex-flow: column nowrap
	min-block-size: 100vb
	max-inline-size: 800px
	margin: 0 auto
	background: $bodybg
	box-shadow: 0 0 0.2rem lightgray
	filter: drop-shadow(0 0 0.2rem lightgray)
	color: $mainColor

a.sr-only
	position: absolute
	left: 0
	top: -2rlh
	padding: 0.25rem 1rem
	outline: unset
	color: #fff
	background-color: #ff0000	

a.sr-only:focus
	top: 0
	color: #fff

header
	block-size: 4.5rem
	display: flex
	flex-flow: row nowrap
	justify-content: space-between
	background: $headerbg
	padding: 0 1.5rem
	margin-bottom: 0.325rem

section
	margin-bottom: 1.25rem

.stitle
	align-self: center

	a
		display: block
		block-size: 2rem

.cool
	block-size: 2rem

.all-content
	flex: 1
	display: flex
	flex-flow: row wrap
	justify-content: space-between

main
	inline-size: 83.5%
	margin-left: 2.5%
	margin-right: 0.5%
	
section > h1:first-child, hgroup h1
	text-align: center
	text-shadow: 0 0 1.5rem rgba($mainColor, 0.75)

.postpage > h2:first-child
	padding-left: 2rem

.postpage h2 + p
	padding-left: 2rem

#myhello
	inline-size: 100%
	display: flex
	flex-flow: row nowrap
	overflow-x: auto
	margin-bottom: calc(3rem - 1px)
	
	p
		font-family: $fonthtml
		font-size: 1rem
		text-align: center
		hyphens: unset

	p::first-letter
		font-weight: inherit
		color: $mainColor	

	.scarec
		align-self: start
		margin: 1rem 0.5rem 0 0

	.helloW
		flex: 3
		
		h1::before
			position: relative
			bottom: 0.125rem
			content: "\1F44B"
			font-family: $fontEmoji
			font-size: 1.5rem
			line-height: 0.8
			margin-right: 0.3rem

aside
	inline-size: 13.5%
	box-sizing: border-box
	
.firstpage
	font-family: $fontContent
	
	p::first-letter
		font-weight: bolder
		color: #800000	

	p
		font-size: 1.5rem

footer
	block-size: 5rem
	display: flex
	flex-flow: row nowrap
	justify-content: space-around
	align-items: center
	background: $footerbg
	padding: 0 1.5rem
	margin-top: 0.5rem
	
	a
		text-decoration: none
		display: block
		block-size: 1.875rem
		
		img
			max-block-size: 100%
		
	.svg-icon
		block-size: 1.875rem
		fill: #696969
	
@import "_menuAll"
@import "_dialog"
@import "_mainstyle"
	
